___CSS_LOADER_EXPORT___.push([module.id, `.add-task-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border: 0;
  /* box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.6); */
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.6);
  border-radius: 14px;
  min-height: 175px;
  width: 690px;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

#add-task-prompt-header,
#add-task-prompt-form {
  display: flex;
  align-items: center;
}

#add-task-prompt-submit {
  display: flex;
}

.add-form-label {
  width: 100%;
}

.add-form-input {
  width: 99.6%;
  font-family: inherit;
  border: 3px solid var(--text-color-light-grey);
  border-radius: 8px;
}

input[id='title'] {
  height: 2em;
  font-size: 1.5em;
  font-weight: 700;
}

textarea {
  resize: none;
  height: 5em;
  color: var(--text-color-hover-black);
}

#add-task-prompt-form {
  margin: 5px 0;
}

#add-task-prompt-submit {
  height: 2em;
}

input[id='date'] {
  cursor: pointer;
  margin-left: 10px;
}

.date {
  display: flex;
  align-items: center;
  flex: 1;
}

.priority {
  flex: 2;
}

/* Custom Select dropdown */

.custom-select-priority {
  max-width: fit-content;
  max-height: fit-content;
}

.custom-select-main {
  display: flex;
}

.custom-select-main,
#options {
  max-width: 66.5px;
}

ul {
  list-style: none;
}

.custom-btn {
  /* background-color: black; */
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0.2rem 0 0.2em 8px;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid var(--main-color-purple);
  position: relative;
}

.dropdown {
  display: none;
  /* Changes to flex on event */
  flex-direction: column;
  gap: 4px;
  margin-top: 4px;
  border: 2px solid var(--text-color-light-grey);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 5px;
  background-color: #fff;
}

.option {
  display: none;
}

.option:hover {
  background-color: var(--main-color-purple);
  cursor: pointer;
}

.option > img {
  padding: 5px 0 0 8px;
}

/* Submit button */

#add-task-submit {
  padding: 6px 38px;
  cursor: pointer;
}
